import React from "react";

export default function AboutMe() {
  return (
    <div>
      <h2>Hello,</h2>
      <p>My name is Çağatay. I studied Translation and Interpreting at
        Istanbul University and graduated in 2013. Studying in Istanbul opened
        up many job possibilities and I did take on various jobs, from ad-hoc
        tourism jobs to a 20-month full-time employment as an assistant project
        director at an international trade fair organisation company.</p>
      <p>In 2012, I decided to freelance as a translator and apart from the
        6-month military service and a year abroad in Belarus, that’s what I
        have been doing all this time. While I initially started off focusing on legal and
        life sciences translation projects in the language pair
        Turkish-English, I gradually shifted towards creative translation,
        or localisation if you will, projects and started to translate from Russian into
        English/Turkish as well.</p>
      <p>I am well-versed in Python. I had taken it up as a hobby, but have
        committed a considerable amount of time to developing Kaplan Desktop, a free
        and open-source CAT project. Visit <a href="https://kaplan.pro/" rel="nofollow noreferrer noopener" target="_blank">the project homepage</a>,
        if you’d like to check it out.</p>
      <p>Best,</p>
    </div>
  );
}
